[data-contrast="low"] {
  --contrast-primary-color: #d7a9a9;
  --contrast-text-color: #3a2a2a;
  --contrast-bg-color: #fff0ec;
}

[data-contrast="medium"] {
  --contrast-primary-color: #c98c92;
  --contrast-text-color: #2c1d1d;
  --contrast-bg-color: #fffaf5;
}

[data-contrast="high"] {
  --contrast-primary-color: #b24f55;
  --contrast-text-color: #1f0f0f;
  --contrast-bg-color: #f8e3e4;
}
